body {
  margin: 0;
}

.header {
  --header-height: 60px;
  --blue-color: #00acdc;
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-family: Roboto, Arial, sans-serif;
  display: flex;
}

.header__nav {
  text-align: center;
  align-items: center;
  margin-right: 50px;
  display: inline-block;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  height: var(--header-height);
  line-height: var(--header-height);
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.nav__item {
  margin: 0;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover {
  color: var(--blue-color);
}

.nav__link--is-active {
  position: relative;
}

.nav__link--is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link--blue {
  color: var(--blue-color);
}

.card__stars--4 {
  display: flex;
}

.card__stars--4 .star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0;
}

.card__stars--4 .star:last-child {
  background-image: url("star.822d561e.svg");
}

.card__stars--4 .star:not(:last-child) {
  margin-right: 4px;
}

.card {
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover__title {
  color: #34568b;
}

.card__tv-image {
  width: 160px;
  height: 134px;
  margin: 0 3px 36px;
}

.card__title {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin: 0 0 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  height: 14px;
  margin: 0 0 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin: 0 0 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-items: center;
  height: 16px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin: 0 0 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  height: 18px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__actual-price {
  text-align: right;
  color: #060b35;
  align-items: center;
  height: 18px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.card__buy-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.catalog {
  --cards-count: 1;
  grid-template-columns: repeat(var(--cards-count), 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --cards-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --cards-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --cards-count: auto-fill;
  }
}

.catalog__card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-top: 32px;
  padding-bottom: 16px;
  padding-left: 16px;
}

